import {useAccountEffect}from'wagmi';const useConnectCallback = ({
  onConnect,
  onDisconnect
}) => {
  useAccountEffect({
    onConnect: ({
      address,
      chainId,
      connector,
      isReconnected
    }) => {
      if (!isReconnected) {
        onConnect?.({
          address,
          chainId,
          connectorId: connector?.id
        });
      }
    },
    onDisconnect: () => {
      onDisconnect?.();
    }
  });
};export{useConnectCallback};